.App {
  display: flex;
  justify-content: center;
  max-width: 500px;
  text-align: center;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

@media screen and (max-width: 300px) {
  .App {
    grid-template-columns: repeat(2, 1fr);
  }
}

.product {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.top {
  margin-top: 40px;
}

.productName {
  margin: 0 auto;
  color: #33cccc;
  font-weight: bold;
  font-size: 110%;
}

.productImg {
  display: block;
  width: 50%;
  margin: 0 auto;
  margin-top: 2px;
  margin-bottom: -3px;
  border-radius: 10px;
}

.price {
  margin-top: 5px;
  color: #33cccc;
}

.add {
  background-color: #33cccc;
  border: none;
  color: white;
  padding: 7px 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 12px;
  max-width: 133px;
  margin: 0 auto;
  border-radius: 10px;
  margin-top: -6px;
}

.count {
  display: flex;
  height: 27.82px;
  margin: 0 auto;
  background-color: #169b9b;
  border-radius: 20px;
  color: #fff;
  margin-top: -6px;
}

.countNumber {
  margin: 3px 10px 0px 10px; 
}

.minus {
  background-color: #33cccc;
  border: none;
  color: white;
  padding: 5px 12px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
  border-radius: 10px;
}

.plus {
  background-color: #33cccc;
  border: none;
  color: white;
  padding: 3px 9px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
  border-radius: 10px
}

.add:active {
  background-color:#169c9c;
}

.minus:active {
  background-color:#169c9c;
}

.plus:active {
  background-color:#169c9c;
}